<template>
    <div class="wk-panel">
        <ul>
            <li>
                <h1></h1>
                <p>{{ apiData.agentBizTown }}</p>
            </li>
            <li v-if="apiData.agentIntroduction">
                <h1></h1>
                <p>{{apiData.agentIntroduction}}</p>
            </li>
            <li v-if="apiData.agentStory">
                <h1></h1>
                <p>{{apiData.agentStory}}</p>
            </li>
        </ul>
    </div>    
</template>

<script>
    import apiDataFilter from "@/libraries/apiDataFilter" ;   
    export default {
      name : "spaceDetailHybrid" ,
      data () {
          return {
              apiData:{}
          }
      },
      created () {
          //
          let agentId=this.$route.params.agentId;
          apiDataFilter.request({
              apiPath : "space.detail" ,
              data : { "agentId" : agentId } ,
              successCallback : res => {
                  let agent = res.body.data.agentDetail ;
                  this.$data.apiData = agent ;
                  console.log(agent);
                  //
                  let generalTitle = "" + agent.agentName ;
                  let shareContent = agent.agentIntroduction || "80%" ;
                  document.title = generalTitle ;  //title
                  //
                  this.$wechatShare({
                      "content" : shareContent ,
                      "imgUrl" : agent.agentHeadImgUrl ,
                      "success" : function() { console.log("") ;  } ,
                      "fail" : function() { console.log("") ;  } ,
                      "cancel" : function() { console.log("") ; } ,
                      "complete" : function() { console.log("") ; }
                  }) ;
              }
          }) ;
      } 
    }
</script>

<style lang="less" scoped>
    @import "../../../less/space/detail/hybrid.less" ;
</style>



