		<!--3.6 -->
		<!--<div class="wk-panel basic-info" v-if="apiData.house.isExternal && apiData.house.extHouseDesc && apiData.house.extHouseDesc.length > 30">
            <h1 class="panel-header"></h1>
            <div  class="panel-body lr-padding tb-padding">
