					<li><a :href="apiData.estate.historicalTransactionListUrl" :data-bigdata="getUvParamsString({ eventName : 2055015,otherParams:{house_id :apiData.house.houseId,estate_id:apiData.estate.estateId}})"><span></span> <span class="count">{{apiData.estate.historicalTransactionAmount}}  <i class="iconfont icon-arrowR"></i></span></a></li>
					<li><a :href="apiData.estate.moreCommentUrl"><span></span> <span class="count">{{apiData.estate.comment&&apiData.estate.comment.amount || 0}}  <i class="iconfont icon-arrowR"></i></span></a></li>
