			<h4 class="panel-header"></h4>
			<esf-sources :cityId="cityId" :agentId="agentId" :items="apiData.sameTownHouseList" :eventName="2055008"  />
		</div>

		<div id="cover">
			<swiper :options="pageConfs.swiperOption2">
	            <swiper-slide  v-for="(slide , index) in houseImageAndVideoList" :key="slide.url">
	            	<div @click="previewImage(true)" class="img-container" :style="{'background-image':'url('+slide.url+')'}"></div>
	                <div class="pagination">{{ index + 1 }} / {{ houseImageAndVideoList.length }}</div>
	            </swiper-slide>
        	</swiper>
		</div>
    </div>
</template>
<script>
	import assistant from "@/components/common/assistant" ;
	import { swiper , swiperSlide } from "vue-awesome-swiper" ;
	import apiDataFilter from "@/libraries/apiDataFilter" ;
	import esfSources from "@/components/common/esfSources" ;
	import downloadApp from "@/components/common/downloadApp" ;
  import offDown from "@/components/common/offDown";
	import $ from 'jquery';
	import Echarts from "echarts";
    import config from "@/configs/api";
    let prefix = config.prefix[apiDataFilter.getEnv()];
	export default {
		name : "esfDetailShare" ,
		components:{assistant,swiper,swiperSlide,esfSources,downloadApp,offDown},
        created(){
            this.houseId = this.$route.params.houseId;
            this.agentId = this.$route.params.agentId;
            this.cityId = this.$route.params.cityId;
            document.title = "";
            //
            this.$bigData({
                pageName : 2055 ,
                pageParam : {
                    agent_id : this.agentId,
                    house_id:this.houseId
                } ,
                type : 1
            }) ;
        },
		data(){
		    return {
              estateId:"",
              agentId:"",
              cityId:"",
              isExpanded: false,
              extHouseDesc:"",
              chartVisible:false,
			  pageStates : {
                  swiperActiveIndex : 1, //
                  hasMoreSwitch : false  //
              } ,
              pageConfs : {
                  swiperOption : {  //  swiper
                      name : "currentSwiper" ,
                      // Swiper
                      autoplay : 0 , //msslide
                      grabCursor : true ,  //trueSwiper
                      setWrapperSize : true ,
                      autoHeight : false ,  //truewrappercontainerslide
                      //
                      onInit : swiper => {
                          this.pageStates.swiperActiveIndex = swiper.activeIndex + 1 ;
                      } ,
                      onSlideChangeEnd : swiper => {
                        this.pageStates.swiperActiveIndex = swiper.activeIndex + 1 ;
                      }
                  },
                  swiperOption2 : {  //  swiper
                      name : "swiper" ,
                      // Swiper
                      autoplay : 0 , //msslide
                      grabCursor : true ,  //trueSwiper
                      setWrapperSize : true ,
                      autoHeight : false ,  //truewrappercontainerslide
                  }
              } ,
              apiData : {
                  house:{//
                    houseId:"",//ID
                    completed:"",//
                    houseChildType:"",//
                    publishDate:"",//
                    unitPrice:"",//
                    extHouseCode:"",//
                    subEstateId:"",//ID
                    latitude:"",
                    longtitude:"",
                    picNum:"",//
                    houseTitle:"",//
                    areaStr:"",//
                    totalPrice:"",//
                    advancePayment:"",//
                    mortgage:"",//
                    floorDesc:"",//
                    decorationStr:"",//
                    orientationStr:"",//
                    houseType:"",//
                    extHouseDesc:"",//
                    owerMotivation:"",//
                    aroundSupport:"",//
                    sellPoint:"",//
                    isWKhouse:"",
                    isStorePush:"",
                    isTopHouse:"",
                    commAgent:"",
                    fullYears:"",
                    isSubwayHouse:"",
                    isSchoolHouse:"",
                    isNewOnStore:"",
                    orientation:"",
                    tagList:[],
                    mortgageUrl:""
                  },
                  estate:{//
                    estateId:"",
                    estateName:"",//
                    town:"",//
                    district:"",//
                    propertyType:"",//
                    estateAddr:"",//
                    completedStr:"",//
                    totalHouse:"",//
                    estateImgUrl:"",//
                    //sellhouseCount:"",//
                    sameEstateHouseAmount:"",//
                    subEstateId:"",//id
                    encryptSubEstateId:"",
                    historicalTransactionAmount:"",//
                    estateUrl:"",
                    schoolName:"",//
                    subwayName:"",//
                    longtitude:"",
                    latitude:"",
                    estateHistoricalPrice:[],
                    comment:""
                  },
                  sameTownTotalCount:"",
                  sameTownHouseList:[],
                  agent:{
                    agentId:"",
                    agentName:"",
                    agentMobile:"",
                    agentHeadImgUrl:"",
                    headRectImgKey:"",
                    headRoundImgUrl:"",
                    headRoundImgKey:"",
                    agentBelongToCompanyName:"",
                    agentCommentScore:"",
                    agentCommentScoreStr:"",
                    shi:"",
                    kong:"",
                    hasSmall:"",
                    agentWChatId:"",
                    agentWChatQRImgUrl:"",
                    weChatQRImgKey:"",
                    isWellAgent:"",
                    agentVerifiedStatus:"",
                    agentBizTown:"",
                    wellAgentBizMessage:"",
                    vipType:"",
                    abbreviation:"",
                    companyName:"",
                    storeName:""
                  }
              } ,
        houseState:4
			}
		},
		mounted(){
            let self = this;
            apiDataFilter.request({
            apiPath:"esf.detail",
              data:{
                houseId:this.houseId,
                agentId:this.agentId
              },
              errorCallback:function(){

              },
              successCallback:function(res){
                let data = res.body;
                data.data.estate.estateUrl = prefix + data.data.estate.estateUrl;//url
                data.data.estate.sameEstateHouseListUrl = prefix + "/estate/sameEstateHouseList.html?subEstateId=" + data.data.estate.encryptSubEstateId;
                data.data.estate.moreCommentUrl=prefix+"/estate/moreComment.html?subEstateId="+data.data.estate.subEstateId+"&commentLocation=1";//
                data.data.estate.historicalTransactionListUrl = prefix + "/estate/historicalTransactionList.html?subEstateId=" + data.data.estate.encryptSubEstateId;
                data.data.house.mortgageUrl = prefix + "/houseLoanCalculator.html?totalPrice="+data.data.house.totalPrice;
                data.data.house.similarListUrl = prefix + "/esf/similarList.html?enCryptHouseId="+self.houseId;


                //document.title = data.data.house.houseTitle;
                self.$nativeBridge.invokeMethod('updateTitle',[data.data.house.houseTitle],function(){
                    console.log("");
                },function(){
                    console.log("");
                })


                self.$wechatShare({
                  "title" : data.data.house.houseTitle ,
                  "timelineTitle" : data.data.house.houseTitle ,
                  "content" : data.data.house.houseTitle ,
                  "imgUrl" : data.data.house.imgList && data.data.house.imgList.length > 0 && data.data.house.imgList[0] || '',//
                  "linkUrl": '',
                  "complete":function(){

                  }
                });

                if(data.data.house.extHouseDesc){
                    self.extHouseDesc = data.data.house.extHouseDesc.substring(0,100);
                };
                Object.assign(self.apiData,data.data);
                  // 3.7
                self.$nextTick(function(){
                    // ;
                     if(self.apiData.house.isWKhouse==2&&self.apiData.house.sellPoint){
                        self.pageStates.hasMoreSwitch = self.$refs.sansInfo.clientHeight > 125 ;
                     }
                    //  erp;
                    if(self.apiData.house.isWKhouse==3&&self.apiData.house.sellPoint){
                        self.pageStates.hasMoreSwitch = self.$refs.sansInfo.clientHeight > 125 ;
                     }
                });
                //tagList
                let house = self.apiData.house;
                house.tagList = [];
                console.log('taglist ', house.tagList);
                if(house.isTopHouse > 1){
                    self.apiData.house.tagList.push({className:"jx",name:""});
                }else{
                    if(house.isStorePush == 1){
                        self.apiData.house.tagList.push({className:"jx",name:""});
                    }else{
                        if(house.commAgent > 0){
                            self.apiData.house.tagList.push({className:"jx",name:""});
                        }
                    }
                }

                if(house.fullYears>=5 && house.onlyOne==1){
                    self.apiData.house.tagList.push({className:"mw",name:""});
                }else{
                    if(house.fullYears >=2){
                        self.apiData.house.tagList.push({className:"mw",name:""});
                    }
                }

                if(house.isSubwayHouse == 1){
                    self.apiData.house.tagList.push({className:"dt",name:""});
                }

                if(house.isSchoolHouse == 1){
                    self.apiData.house.tagList.push({className:"jxx",name:""});
                }

                if(house.isNewOnStore == 1){
                    self.apiData.house.tagList.push({className:"xs",name:""});
                }

                if(house.orientation == 9){
                    self.apiData.house.tagList.push({className:"tt",name:""});
                }

                if(house.houseId > 1000000000){
                    self.apiData.house.isExternal  = true;
                }else{
                    self.apiData.house.isExternal  = false;
                }

                //taken from h5 
                var datatmp = [], unitprice = [],realPrice=[];
                var unSortPrice,maxPrice,tmpPrice;
                if (data.data.estate.estateHistoricalPrice && data.data.estate.estateHistoricalPrice.length > 1)  {
                        self.chartVisible = true;
                        for (var i = 0; i < data.data.estate.estateHistoricalPrice.length; i++) {
                            if (data.data.estate.estateHistoricalPrice[i] && data.data.estate.estateHistoricalPrice[i].date && data.data.estate.estateHistoricalPrice[i].unitPrice) {
                                tmpPrice=parseFloat(data.data.estate.estateHistoricalPrice[i].unitPrice) == 0 ? null : parseFloat(data.data.estate.estateHistoricalPrice[i].unitPrice);
                                datatmp.push(parseInt(data.data.estate.estateHistoricalPrice[i].date.split('-')[1]) + '');

                                unitprice.push(tmpPrice);

                                if (tmpPrice) {
                                    realPrice.push(tmpPrice);
                                }
                            }

                        }

                        unSortPrice = unitprice.slice();

                        var sortArray = realPrice.sort(function(a, b) {
                            return parseFloat(a) - parseFloat(b);
                        });
                        maxPrice = Math.ceil((sortArray[sortArray.length - 1] / 10000) + 1) * 10000;

                        let myChart = Echarts.init(document.getElementById('price'));
                        // 
                        let option = {
                            title: {
                                text: ''
                            },
                            tooltip: {
                                trigger: 'item',
                                triggerOn: 'click',
                                position: 'top',
                                backgroundColor: '#92A7C3',
                                padding: 4,
                                textStyle: {
                                    color: '#fff',
                                    fontSize: '12'
                                },
                                formatter: '{c}'
                            },

                            grid: {
                                top: '20',
                                bottom: '20',
                                left: '5%',
                                right: '5%',
                                containLabel: true,
                            },
                            xAxis: [{
                                splitLine: {
                                    show: false
                                },
                                position: 'bottom',
                                type: 'category',
                                boundaryGap: false,
                                axisLine: {
                                    show: false
                                },
                                axisTick: { //
                                    show: false
                                },
                                axisLabel: {
                                    interval: 0,
                                    rotate: 0, //60
                                    textStyle: {
                                        color: '#999',
                                        fontSize: 12,
                                        fontFamily: ''
                                    }
                                },
                                data: datatmp
                            }],
                            yAxis: [{
                                boundaryGap: true,
                                axisLine: {
                                    show: false
                                },
                                axisTick: {
                                    show: false
                                },
                                axisLabel: {
                                    inside: false,
                                    margin: 20,
                                    textStyle: {
                                        color: '#999',
                                        fontSize: 12,
                                        fontFamily: ''
                                    },
                                    formatter: function(value, index) {
                                        if (value == 0) {
                                            return "";
                                        } else {
                                            return value / 10000 + '';
                                        }
                                    }
                                },
                                splitLine: {
                                    lineStyle: {
                                        type: 'solid',
                                        color:'#979797'
                                    }
                                },
                                position: 'left',
                                type: 'value',
                                min: 0,
                                max: maxPrice,
                                splitNumber: 4,
                                interval: maxPrice / 4
                            }],
                            series: [{
                                type: 'line', //
                                symbol: 'circle',
                                symbolSize: 6,
                                connectNulls: true,
                                areaStyle: {
                                    normal: {
                                        // color: '#fff'
                                        opacity:0
                                    }
                                },
                                itemStyle: { //
                                    normal: {
                                        color: '#fff',
                                        borderColor: '#92A7C3',
                                        borderWidth: 2,
                                    },
                                    emphasis: { //
                                        borderColor: '#92A7C3',
                                        borderWidth: 2,
                                        color: '#92A7C3'
                                    }
                                },
                                lineStyle: {
                                    normal: {
                                        color: '#92A7C3'
                                    }
                                },
                                data: unSortPrice
                            }]
                        };
                        // 
                        myChart.setOption(option);

                    }else{
                        console.log('price hide');
                        //$('#price').hide().prev().hide();
                        self.chartVisible = false;
                    }
                }
            });
		},
		methods : {
          //
          getUvParamsString : function({ eventName , otherParams }) {
            //   let eventParam = { house_id : this.houseId } ;
              let eventParam={};
              if(otherParams !== undefined && otherParams !== null ) {
                  eventParam = Object.assign( eventParam , otherParams ) ;
              }
              return encodeURIComponent(JSON.stringify({
                  eventName : eventName ,
                  eventParam : eventParam ,
                  type : 2
              })) ;
          },
          previewImage(hide){
          	// if(hide){
          	// 	$('#cover').hide();
           //      $('html').css("overflow","auto");
          	// }else{
          	// 	$('#cover').show();
           //      $('html').css("overflow","hidden");
          	// }
          },
          playVideo(video){
            this.$router.push({
                path:"/videoPlay?video=" + video
            });
          },
          toggleExpand(){
            this.isExpanded = !this.isExpanded;
            if(this.isExpanded){//
                this.extHouseDesc = this.apiData.house.extHouseDesc;
            }else{//
                this.extHouseDesc = this.apiData.house.extHouseDesc.substring(0,100);
            }
          },
          //
          spreadDescription(){
            this.pageStates.hasMoreSwitch = false ;
          }
        } ,
        computed:{
            houseImageAndVideoList:function(){
                let result = [];
                if(this.apiData.house.houseVideoResponse){
                    result.push({
                        isVideo: true,
                        video: encodeURIComponent(JSON.stringify({
                            videoUrl:this.apiData.house.houseVideoResponse.videoUrl,
                            videoSmallImage: this.apiData.house.houseVideoResponse.videoSmallImage
                        })),
                        url:this.apiData.house.houseVideoResponse.videoUrl,
                        videoUrl:this.apiData.house.houseVideoResponse.videoUrl,
                        videoSmallImage: this.apiData.house.houseVideoResponse.videoSmallImage
                    });
                }

                //https://img.wkzf.com/cf931c293aab471aa5b4a10db350922c.CL
                //http://v.wkzf.com/d629583119e942beb4aeb456a81bae48WV.mp4

                // result.push({
                //     isVideo: true,
                //     video: encodeURIComponent(JSON.stringify({
                //         videoUrl:"http://v.wkzf.com/d629583119e942beb4aeb456a81bae48WV.mp4",
                //         videoSmallImage: "https://img.wkzf.com/cf931c293aab471aa5b4a10db350922c.CL"
                //     })),
                //     url:"https://img.wkzf.com/cf931c293aab471aa5b4a10db350922c.CL",
                //     videoUrl:"http://v.wkzf.com/d629583119e942beb4aeb456a81bae48WV.mp4",
                //     videoSmallImage: "https://img.wkzf.com/cf931c293aab471aa5b4a10db350922c.CL"
                // });

                // result.push({
                //     url:"https://img.wkzf.com/cf931c293aab471aa5b4a10db350922c.CL"
                // });

                // result.push({
                //     url:"https://img.wkzf.com/cf931c293aab471aa5b4a10db350922c.CL"
                // });

                // result.push({
                //     url:"https://img.wkzf.com/cf931c293aab471aa5b4a10db350922c.CL"
                // });

                if(this.apiData.house.imgList){
                    this.apiData.house.imgList.forEach(function(img){
                        result.push({
                            url:img
                        });
                    });
                }

                return result;
            },
            mapImgUrl:function(){
                return 'https://api.map.baidu.com/staticimage/v2?ak=GByG2pAz1WlmY7wX1rlIM4nd&width=640&height=426&center='+this.apiData.estate.longitude+','+this.apiData.estate.latitude+'&zoom=18';
            },
            mapUrl:function(){
                return prefix + '/esf/map.html?longitude=' + this.apiData.estate.longitude + '&latitude=' + this.apiData.estate.latitude + '&houseName='+this.apiData.estate.estateName + '&houseAddress=' + this.apiData.estate.estateAddr;
            }
        }
	}
</script>
<style lang="less">
	@import "../../../less/esf/detail.less";

    .swiper-wrapper{
        img{
            width:100%;
            height:210px;
        }
    }
</style>



