import Vue from "vue" ;
import VueResource from "vue-resource" ;  //
import App from "./App" ;
import router from "./router" ;
import VueLazyload from "vue-lazyload" ;  //
import WechatShare from "@/plugins/wechatShare";
import BigData from "@/plugins/bigData";
import NativeBridge from "@/plugins/nativeBridge";
import DeviceId from "@/plugins/deviceId";
import VueCookie from "vue-cookie" ;  //cookie

/*++-----------------------------------------------------------------------------------------------------------------------------------------------------------------------
vue
-----------------------------------------------------------------------------------------------------------------------------------------------------------------------++*/
Vue.use(VueResource) ;
Vue.use(VueLazyload) ;
Vue.use(WechatShare) ;
Vue.use(DeviceId) ;
Vue.use(BigData) ;
Vue.use(NativeBridge) ;
Vue.use(VueCookie) ;
/*++-----------------------------------------------------------------------------------------------------------------------------------------------------------------------

-----------------------------------------------------------------------------------------------------------------------------------------------------------------------++*/
Vue.config.productionTip = false ;
/*++-----------------------------------------------------------------------------------------------------------------------------------------------------------------------
vue
-----------------------------------------------------------------------------------------------------------------------------------------------------------------------++*/
new Vue({
  el : "#app" ,
  router ,
  template : "<App/>" ,
  components : { App }
}) ;


