<template>
	<div>
		: {{title}}
	</div>
</template>
<script>
export default {
	name: "updateTitle",
	data: function(){
		return {
			title: ""
		};
	},
	created: function(){
		let self = this;
		setTimeout(function(){			
			self.$nativeBridge.invokeMethod('updateTitle',[self.title],function(){
				self.title = "";
			},function(){
				self.title = "";
			});
		},3000);		
	}
}
</script>
<style>
	
</style>


